import React, { useCallback } from 'react';
import { Grid, IconButton, makeStyles, Theme, Tooltip, Typography } from '@material-ui/core';
import Image, { FluidObject } from 'gatsby-image';
import * as Icon from 'react-feather';
import { Logger } from 'utils/loggers';

const useStyles = makeStyles<Theme>(({ spacing, typography, palette, breakpoints }) => ({
  root: {
    marginBottom: spacing(6),
  },
  logo: {
    minWidth: 50,
    marginRight: spacing(2),
    [breakpoints.down('xs')]: {
      display: 'none',
    },
  },
  orgName: {
    fontWeight: typography.fontWeightBold,
    fontSize: typography.h6.fontSize,
  },
  role: {
    marginBottom: spacing(2),
    fontSize: typography.caption.fontSize,
    color: palette.text.secondary,
  },
}));

interface Props {
  logo: FluidObject;
  orgName: string;
  role: string;
  children: string;
  link: string;
  logger?: Logger;
}
const OrganizationItem = ({ logo, orgName, role, link, children, logger }: Props) => {
  const styles = useStyles();

  const handleExternalLinkClick = useCallback(() => {
    logger?.click('click_external_org_link', { target: orgName });
  }, [logger, orgName]);

  return (
    <Grid className={styles.root} container>
      <Grid item>
        <Image className={styles.logo} fluid={logo} />
      </Grid>
      <Grid item xs={10}>
        <Typography className={styles.orgName} component="h3">
          {orgName}
        </Typography>
        <Typography className={styles.role}>{role}</Typography>
        <Typography variant="body2">{children}</Typography>
      </Grid>
      <Grid item xs={12} sm>
        <IconButton href={link} target="_blank" onClick={handleExternalLinkClick}>
          <Tooltip title="더 알아보기">
            <Icon.Link />
          </Tooltip>
        </IconButton>
      </Grid>
    </Grid>
  );
};

export default OrganizationItem;
